@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Ubuntu+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  height: 100vh;
  font-family: Poppins;
}

.StoriesBoard {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-start;
          align-items: flex-start;

  width: 100%;
  margin-left: 5vh;
  padding-top: 5vh;
}

.StoriesRow {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-align-items: flex-end;
          align-items: flex-end;

  margin-bottom: 45px;
}

.StorieTitle {
  font-family: Ubuntu Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 12px;
  width: 100%;
  margin-bottom: 12px;
}

.Storie {
  width: 300px;
  text-align: start;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Title {
  font-size: 24px;
  font-weight: 500;
  font-family: "Poppins", sans-serif;
  color: #4f4f4f;
  line-height: 36px;
  margin-bottom: 28px;
}

.SubTitle {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;

  /* identical to box height */
  text-align: center;

  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  color: #a9a9a9;

  background-color: white;
  text-align: center;
}

.Button{border:none;background-color:#e0e0e0;color:#3f3f3f;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;box-shadow:0px 2px 3px rgba(51,51,51,.2);font-family:Noto Sans JP;line-height:20px;font-style:normal;font-weight:500;border-radius:6px;padding-left:20px;padding-right:20px;font-size:14px;max-width:-webkit-fit-content;max-width:-moz-fit-content;max-width:fit-content}.Button:hover,.Button:focus,.Button.hover{cursor:pointer;background-color:#aeaeae}.sm{height:32px}.md{height:36px}.lg{height:42px}.Button.outline{border:1px solid #3d5afe;background-color:transparent;color:#3d5afe}.Button.outline:hover,.Button.outline:focus,.Button.outline.hover{background-color:rgba(41,98,255,.1)}.Button.text{border:none;background-color:transparent !important;color:#3d5afe;box-shadow:none}.Button.text:hover,.Button.text:focus,.Button.text.hover{background-color:rgba(41,98,255,.1) !important}.Button.disableShadow{box-shadow:none}.Button.disabled{box-shadow:none;background-color:#e0e0e0;color:#9e9e9e}.Button.disabled:hover,.Button.disabled:focus{color:#e0e0e0}.ColorDefault{background-color:#e0e0e0}.ColorDefault:hover,.ColorDefault:focus{background-color:#aeaeae}.ColorPrimary{background-color:#2962ff;color:#fff}.ColorPrimary:hover,.ColorPrimary:focus,.ColorPrimary.hover{background-color:#0039cb}.ColorSecondary{background-color:#455a64;color:#fff}.ColorSecondary:hover,.ColorSecondary:focus,.ColorSecondary.hover{background-color:#1c313a}.ColorDanger{background-color:#d32f2f;color:#fff}.ColorDanger:hover,.ColorDanger:focus,.ColorDanger.hover{background-color:#9a0007}
.Menu{width:237px;height:100%;min-height:100;background-color:#fafbfd;padding:46px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;text-align:left}.Menu .Title{font-family:"Poppins",sans-serif;display:-webkit-flex;display:flex;font-weight:600;font-size:13px;line-height:19px;margin-bottom:20vh}.Menu .TitleFirst{color:#f7542e}.Menu .Element{color:#9e9e9e;font-family:Noto Sans JP;font-weight:500;font-size:14px;line-height:20px;margin-top:30px;cursor:pointer}.Menu .Element.Active{color:#090f31}
